/* global $, jQuery, document, Site, Modernizr */

Site = {
  mobileThreshold: 601,
  init: function() {
    var _this = this;

    $(window).resize(function(){
      _this.onResize();
    });

    $(document).ready(function () {
      _this.Video.init();
    });

  },

  onResize: function() {
    var _this = this;

  },

  fixWidows: function() {
    // utility class mainly for use on headines to avoid widows [single words on a new line]
    $('.js-fix-widows').each(function(){
      var string = $(this).html();
      string = string.replace(/ ([^ ]*)$/,'&nbsp;$1');
      $(this).html(string);
    });
  },
};

Site.Video = {
  threshold: 300, //in px
  init:  function() {
    var _this = this;

    _this.$video = $('#splash-video');

    _this.$video.on('canplaythrough', _this.handleCanPlayThrough.bind(_this));

  },

  handleCanPlayThrough: function() {
    var _this = this;

    _this.videoDuration = _this.$video[0].duration;

    _this.bindScroll();
  },

  bindScroll: function() {
    var _this = this;

    _this.videoScroll = scrollMonitor.create(_this.$video[0], { top: _this.threshold });

    _this.videoScroll.fullyEnterViewport(function() {
      _this.$video[0].play();
    });

  }
};

Site.init();
